.chroma .ln { min-width: 50px; width: 50px; display: inline-block; text-align: right; }

/* Background */

.chroma { color: #8a8a8a; background-color: transparent; }

/* Other */

.chroma .x { color: #d75f00 }

/* LineTableTD */

.chroma .lntd { vertical-align: top; padding: 0; margin: 0; border: 0; }

/* LineTable */

.chroma .lntable { border-spacing: 0; padding: 0; margin: 0; border: 0; width: auto; overflow: auto; display: block; }

/* LineHighlight */

.chroma .hl { display: block; width: 100%;background-color: #ffffcc }

/* LineNumbersTable */

.chroma .lnt { margin-right: 0.4em; padding: 0 0.4em 0 0.4em;color: #454545 }

/* LineNumbers */

.chroma .ln { margin-right: 0.4em; padding: 0 0.4em 0 0.4em;color: #8e8d8b; }

/* Keyword */

.chroma .k { color: #cc7833 }

/* KeywordConstant */

.chroma .kc { color: #d75f00 }

/* KeywordDeclaration */

.chroma .kd { color: #0087ff }

/* KeywordNamespace */

.chroma .kn { color: #d75f00 }

/* KeywordPseudo */

.chroma .kp { color: #5f8700 }

/* KeywordReserved */

.chroma .kr { color: #0087ff }

/* KeywordType */

.chroma .kt { color: #af0000 }

/* Name */

.chroma .n { color: #FFC66D }

/* NameBuiltin */

.chroma .nb { color: #0087ff }

/* NameBuiltinPseudo */

.chroma .bp { color: #0087ff }

/* NameClass */

.chroma .nc { color: #0087ff }

/* NameConstant */

.chroma .no { color: #d75f00 }

/* NameDecorator */

.chroma .nd { color: #0087ff }

/* NameEntity */

.chroma .ni { color: #d75f00 }

/* NameException */

.chroma .ne { color: #af8700 }

/* NameFunction */

.chroma .nf { color: #0087ff }

/* NameTag */

.chroma .nt { color: #0087ff }

/* NameVariable */

.chroma .nv { color: #0087ff }

/* LiteralString */

.chroma .s { color: #00afaf }

/* LiteralStringAffix */

.chroma .sa { color: #00afaf }

/* LiteralStringBacktick */

.chroma .sb { color: #4e4e4e }

/* LiteralStringChar */

.chroma .sc { color: #00afaf }

/* LiteralStringDelimiter */

.chroma .dl { color: #00afaf }

/* LiteralStringDoc */

.chroma .sd { color: #00afaf }

/* LiteralStringDouble */

.chroma .s2 { color: #00afaf }

/* LiteralStringEscape */

.chroma .se { color: #af0000 }

/* LiteralStringHeredoc */

.chroma .sh { color: #00afaf }

/* LiteralStringInterpol */

.chroma .si { color: #00afaf }

/* LiteralStringOther */

.chroma .sx { color: #00afaf }

/* LiteralStringRegex */

.chroma .sr { color: #af0000 }

/* LiteralStringSingle */

.chroma .s1 { color: #AA8FC2 }

/* LiteralStringSymbol */

.chroma .ss { color: #00afaf }

/* LiteralNumber */

.chroma .m { color: #00afaf }

/* LiteralNumberBin */

.chroma .mb { color: #00afaf }

/* LiteralNumberFloat */

.chroma .mf { color: #00afaf }

/* LiteralNumberHex */

.chroma .mh { color: #00afaf }

/* LiteralNumberInteger */

.chroma .mi { color: #00afaf }

/* LiteralNumberIntegerLong */

.chroma .il { color: #00afaf }

/* LiteralNumberOct */

.chroma .mo { color: #00afaf }

/* OperatorWord */

.chroma .ow { color: #5f8700 }

/* Comment */

.chroma .c { color: #4e4e4e }

/* CommentHashbang */

.chroma .ch { color: #4e4e4e }

/* CommentMultiline */

.chroma .cm { color: #4e4e4e }

/* CommentSingle */

.chroma .c1 { color: #629755 }

/* CommentSpecial */

.chroma .cs { color: #5f8700 }

/* CommentPreproc */

.chroma .cp { color: #5f8700 }

/* CommentPreprocFile */

.chroma .cpf { color: #5f8700 }

/* GenericDeleted */

.chroma .gd { color: #af0000 }

/* GenericEmph */

.chroma .ge { font-style: italic }

/* GenericError */

.chroma .gr { color: #af0000; font-weight: bold }

/* GenericHeading */

.chroma .gh { color: #d75f00 }

/* GenericInserted */

.chroma .gi { color: #5f8700 }

/* GenericStrong */

.chroma .gs { font-weight: bold }

/* GenericSubheading */

.chroma .gu { color: #0087ff }
